@import url(https://fonts.googleapis.com/css2?family=Fjalla+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Fjalla+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Big+Shoulders+Inline+Text:wght@600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Fjalla+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Big+Shoulders+Inline+Text:wght@600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Fjalla+One&display=swap);
.navbar{
    font-family: 'Fjalla One', sans-serif;
    font-size: 14pt;
    text-transform: uppercase;
    background: transparent;
    padding-right: 0;
}
.portfolio-header {
    font-family: 'Fjalla One', sans-serif;
    font-size: 45px;
    text-transform: uppercase;
}

#card-column {
    margin-left: 2rem !important;
    margin-right: 2rem !important;
}

.card-header {
    justify-content: center;
    font-family: 'Fjalla One', sans-serif;
    text-transform: uppercase;
}

.card-header .fa {
    transition: .3 transform ease-in-out;
}

.card-header .collapsed .fa {
    transform: rotate(90deg);
}

#image-card {
    width: 18rem;
    height: 15.5rem;
    background-size: cover;
}

#body-card {
    width: 18rem;
    background: linear-gradient(white, cornflowerblue);
    border: transparent;
}

.project-name {
    font-family: 'Fjalla One', sans-serif;
    text-transform: uppercase;
    font-size: 20px;
}

.card-body {
    color: black;
}

.pull-right {
    float: right;
}
.home-page {
    background-image: url(/static/media/hero.9e4703b2.png);
    background-size: cover;
    background-repeat: no-repeat;
    margin-left: 0;
    margin-right: 0;
    padding-top: 12rem;
    padding-bottom: 20rem;
}


.heading {
    font-family: "Big Shoulders Inline Text", cursive;
    font-size: 65px;
    color: white;
    text-transform: uppercase;
    text-align: center;
    padding-top: 2rem;
    padding-bottom: 2rem;
}

.heading-text {
    font-family: 'Fjalla One', sans-serif;
    font-size: 25px;
}

#home-button {
    width: 220px;
    background-color: thistle;
    border-color: thistle;
    border-radius: 4px;
    font-family: 'Fjalla One', sans-serif;
    font-size: 20px;
    text-transform: uppercase;
    transition-duration: 0.4s;
    margin-top: 6rem;
}

#home-button:hover {
    background-color: darkgrey;
}
#profile-img {
    max-width:30%;
    max-height:30%;
    display: block;
    margin-left: auto;
    margin-right: auto;
    margin-top: 2rem;
    margin-bottom: 2rem;
}

#about-header {
    font-family: "Big Shoulders Inline Text", cursive;
    font-size: 50pt;
    color:white;
    text-transform: uppercase;
    text-align: center;
    padding-top: 5rem;
}

#about-name {
    font-family: 'Fjalla One', sans-serif;
    font-size: 28pt;
    color: white;
    text-transform: uppercase;
    text-align: center;
}

#about-title {
    font-family: 'Fjalla One', sans-serif;
    font-size: 18pt;
    color:white;
    text-transform: uppercase;
    text-align: center;
}
#contact-header {
    font-family: 'Fjalla One', sans-serif;
    font-size: 28pt;
    color: white;
    text-transform: uppercase;
    text-align: center;
}

#contact-body {
    font-family: Georgia, 'Times New Roman', Times, serif;
    font-size: 18px;
}

.contact-links {
    color: rgb(63, 30, 182) !important;
}
body {
    color: whitesmoke;
    text-align: center;
}

footer {
    position: fixed;
    width: 100%;
    bottom: 0;
    left: 0;
    background-color: rgb(43, 41, 41);
    align-items: center;
}

.footer-icons {
    justify-content: center;
}
body {
    background-color: cornflowerblue;
    margin-bottom: 100px;
}
